.darkMode {
  color: var(--text-white);
  background: none;
  border: none;
  font-size: 12.5px;
  display: flex;
  gap: 10px;
  padding: 7.5px 15px;
  border-radius: 10px;
  align-items: center;
  letter-spacing: 0.5px;
}

.darkMode button:hover {
  background-color: var(--main-bg);
}
